.cardContainer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 2rem;
}

.button {
  width: 2rem;
  height: 1.5rem;
  cursor: pointer;
  display: flex;
  margin: 0.3rem;
  align-items: center;
  justify-content: center;
  border-radius: 40%;
  border: none;
  background-color: #b8820d;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.button2 {
  width: 2.2rem;
  height: 2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40%;
  border: none;
  background-color: #0db80d;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.prev {
  width: 4rem;
  height: 1.5rem;
  cursor: pointer;
  display: flex;
  margin: 0.3rem;
  color: wheat;
  align-items: center;
  justify-content: center;
  border-radius: 50% 0 0 50%;
  border: none;
  background-color: #0db80d8a;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.prevMax {
  width: 4rem;
  height: 1.5rem;
  display: flex;
  margin: 0.3rem;
  color: wheat;
  align-items: center;
  justify-content: center;
  border-radius: 50% 0 0 50%;
  border: none;
  background-color: #b80d0d;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.next {
  width: 4rem;
  height: 1.5rem;
  cursor: pointer;
  display: flex;
  margin: 0.3rem;
  color: wheat;
  align-items: center;
  justify-content: center;
  border-radius: 0 50% 50% 0;
  border: none;
  background-color: #0db80d8a;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.nextMax {
  width: 4rem;
  height: 1.5rem;
  display: flex;
  margin: 0.3rem;
  color: wheat;
  align-items: center;
  justify-content: center;
  border-radius: 0 70% 70% 0;
  border: none;
  background-color: #b80d0d;
  margin-bottom: -1rem;
  font-size: 1rem;
  filter: drop-shadow(2px 4px 8px #9c690a);
  text-shadow: 0 0 0.1em white, 0 0 0.45em #1310aa;
}

.prev:hover {
  background-color: #0db80d;
}

.next:hover {
  background-color: #0db80d;
}

.button:hover {
  box-shadow: inset 0 0 0.5em 0 rgb(112, 216, 147), 0 0 0.5em 0 rgb(112, 216, 147);
  background-color: cadetblue;
}